@import url(https://fonts.googleapis.com/css2?family=Hachi+Maru+Pop&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.intro{
    height: 250px;
    width: 100%;
    background-image: url(/recipeApp/static/media/foodimage.9e657e48.jpg);
    background-size:cover;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.8;
    margin-top: -38px;

}
.intro h1{
    color: white;
    text-align: center;
    margin-top: 5vh;
    font-size: 40px;
    z-index: 999;
    position: relative;
    top: 20px;
  
}
.search{
    text-align: center;
}
.search input{
    border: none;
    outline: none;
    height: 30px;
    width: 300px;
    margin-right: 7px;
    border-radius: 3px;
    padding-left: 10px;
    font-weight: bold;
    margin-top: 30px;
}
.search input:focus{
    box-shadow: 0 0 5px rgb(47, 54, 61);
    border: 1px solid rgb(44, 48, 59) ;
}
.search button{
    height: 32px;
    outline: none;
    border-radius: 3px;
    border: none;
    cursor: pointer;
    font-weight: bold;
    background-color: rgb(8, 29, 88);
    color: white;
    padding: 2px 15px;
    border: 1px solid rgb(44, 48, 59) ;
}
.itemCards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  
}
.dev p{
    font-family: 'Hachi Maru Pop', cursive;
    color: white;
    font-size: 20px;
    border: black solid 1px;
    background-color: black;
    text-align: center;
    height: 45px;
    margin-top: -14px;
    margin-bottom: 0px;
   padding-top: 3px;
   border: black solid 1px;
}
@media (max-width:768px){

    .intro{
        width: 101%;
    }
    .search input{
        width: 300px;
    }
    .itemCards{
        display: flex;
        flex-direction: column;
    }
    .intro h1{
        position: relative;
        top: 35px;
    }
    .search input{
        margin-top: 40px;
        width: 250px;
    }
    .dev p{
        height: 65px;
        width: 100.5%;
    }
}

.cards{
    text-align: center;
    border: black solid 2px;
    width: 32vw;
    background-color: rgba(223, 49, 165, 0.123);
    margin-bottom: 15px;
}
.cards .title{
    background-color: rgba(0, 0, 0, 0.61);
    margin-top: 0px;
    border-bottom: solid 1px black;
    color: rgb(255, 255, 255);
    font-style:oblique;
    
}
.cards img{
    width: 80%;
    height: 300px;
    border-radius: 10px;
    box-shadow: 0 0 5px rgb(47, 54, 61);
}
.cards .calories{
    background-color: rgba(47, 224, 255, 0.384);
    height: 30px;
    border-bottom: solid 1px black;
    font-style: italic;
}
.cards .items{
    background-color: rgba(172, 255, 47, 0.63);
    /* background-color: rgba(47, 224, 255, 0.384); */
    height: 40px;
    border-bottom: solid 1px black;
    margin-top: -13px;

}
.cards ul{
 text-align: start;
 font-size: 20px;

}
@media (max-width:768px){

    .cards{
        width: 100%;
    }
}
